// get human readerable label of gender and category with label or code
export function getLabel(entity) {
  if (entity.label && LABELS[entity.label]) {
    return LABELS[entity.label]['en-US'];
  } else if (entity.label) {
    return entity.label;
  }
  if (entity.code && LABELS[entity.code]) {
    return LABELS[LABELS[entity.code]]['en-US'];
  }
  if (entity.code && CATEGORY[entity.code.toString()]) {
    return CATEGORY[entity.code.toString()].str;
  }
  return entity;
}

// legacy version of getLabel
export function getCategoryName(region) {
  if (region.category1 && LABELS[region.category1.label]) {
    return LABELS[region.category1.label]['en-US'];
  }
  else if (region.category && LABELS[region.category.code] &&
      ['c1', 'c2', 'c3'].indexOf(LABELS[region.category.code].split('.')[0]) > -1 &&
      LABELS[LABELS[region.category.code]]) {
    return LABELS[LABELS[region.category.code]]['en-US'];
  }
  else if (CATEGORY[region.category.code.toString()]) {
    return CATEGORY[region.category.code.toString()].str;
  }
  return '';
}

